.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

.wrapper {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.title {
  text-align: left;
}

.infoList {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 10px;
  list-style: none;
}


.item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  width: 180px;
  opacity: .8;
  text-transform: capitalize;
  font-size: 15px;
  line-height: 24px;
}

.details {
  text-transform: initial;
  font-weight: 600;

}

.links {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}



@media (max-width: 970px) {
  .infoList {
    max-width: 400px;
    margin: 0 auto;
  }
  .links {
    flex-direction: column-reverse;
    gap: 20px
  }
}

@media (max-width: 600px) {
  .codewarsImg {
    width: 100%;
  }
  .title {
    text-align: center;
  }
}

@media (max-width: 470px) {
  .item {

  }
}

