@import './src/styles/mixins';

.wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  animation: fadeIn 1s forwards;
  gap: 15px;
}


.wrapperLists {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  animation: fadeIn 1s forwards;
}

.contactsList {
  width: 70%;
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.societiesList {
  width: 40%;
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
}



@keyframes fadeIn{
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}



@media (max-width: 800px) {
  .wrapper {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .contactsList {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 15px;
  }

  .societiesList {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    gap: 15px;
  }
}