.link {
  display: block;
  height: 40px;
  width: 40px;
  line-height: 40px;
  text-align: center;
  transition: .3s;
  font-size: 17px;
  margin: 0 6px;
  border-radius: 50%;
  color: #666;
  background: #eee;
}

.link:hover,
.link:active,
.link:focus {
  background-color: #ffb400;
  color: #fff;
}