.skill {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  width: 150px;
  height: 150px;
  color: #666;
}

.imgContainer {
  display: flex;
  justify-content: center;
  width: 120px;
  margin: 15px auto;
}

.img {
  width: 80%;
  border-radius: 20px;
  padding: 10%;
  box-shadow: 0 5px 10px 3px rgba(0, 0, 0, 0.6);
  transition: 0.3s;
}


.title {
  text-transform: uppercase;
  margin-bottom: 5px;
  font-size: 1.6rem;
}
